import Categories from './category-bar';
import React, {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

function NavBar() {
  return (
    <div className="NavBar">
      <Link className="NavBarElement" href="/">Главная</Link>
      <div className="NavBarElement">
        <Link className="NavBarElement" href="/categories">Категории</Link>
        <div className="HiddenBar">
            <Categories />
        </div>
      </div>
      <Link className="NavBarElement" href="0">О проекте</Link>
    </div>
  );
}

export default NavBar;
